}

:root {
  --text-font: "Montserrat", sans-serif;
  --title-font: "Times New Roman", sans-serif;
  --logo-font: "FilmP3", sans-serif;
  --background-main-color: #fffdee;
  --background-section-color-accent: #f0ecd6;
  --text-main-color: #7a7a7a;
  --text-accent-color: #c79002;
  --main-white: #fff;
  --secondary-white: #e5e5e5;
  --main-black: #000;
  --background-btn-dark-shadow: rgba(0, 0, 0, .4);
  --subtitle-color: #b4b4b4;
  --accent-yellow: #f3c653;
  --logo-dark-color: #494848;
  --modal-text-input: gray;
  --footer-color: #0e0c07;
  --background-color-for-pictures: #dadada;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

h1, h2, h3, h4, p, ul {
  margin-top: 0;
  margin-bottom: 0;
}

ul {
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

