/* ======================section 2room-luxury============================ */
.luxury-room {
    padding-top: 32px;
    padding-bottom: 32px;
    position: relative;
}

@media screen and (min-width: 1024px) {
    .luxury-room {
        padding-top: 64px;
        padding-bottom: 40px;
    }
}

@media screen and (min-width: 1280px) {
    .luxury-room {
        padding-top: 72px;
    }
}

.content-room {
    font-family: 'TimesNewRoman';
    font-weight: 400;
    font-size: 20px;
    line-height: calc(20 / 23);
    color: var(--subtitle-color);
    margin-bottom: 8px;
}

@media screen and (min-width: 1024px) {
    .content-room {
        font-size: 32px;
        line-height: calc(37 / 32);
    }
}

@media screen and (min-width: 1280px) {
    .content-room {
        margin-bottom: 4px;
    }
}

.logo-lux-room {
    font-family: 'FilmP3';
    font-size: 40px;
    line-height: calc(43/40);
    letter-spacing: 0.30em;
    color: var(--logo-dark-color);
}

@media screen and (min-width: 1024px) {
    .logo-lux-room {
        font-size: 64px;
        line-height: calc(69 / 64);
    }
}

.title-room {
    font-family: 'TimesNewRoman';
    font-weight: 700;
    font-size: 24px;
    line-height: calc(28 / 24);
    letter-spacing: 0.05em;
    color: var(--main-black);
    margin-bottom: 12px;
    margin-top: 32px;
}

@media screen and (min-width: 1024px) {
    .title-room {
        font-size: 34px;
        line-height: calc(35 / 30);
    }
}

@media screen and (min-width: 1280px) {
    .title-room {
        font-size: 41px;
        line-height: calc(41 / 36);
    }
}

.content-room_subtitle {
    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 12px;
    line-height: calc(15 / 12);
    letter-spacing: 0.02em;
    color: #5F5F5F;
    width: 258px;
    margin-bottom: 32px;
}

@media screen and (min-width: 1024px) {
    .content-room_subtitle {
        font-size: 16px;
        line-height: calc(19 / 16);
        width: 439px;
    }
}

@media screen and (min-width: 1280px) {
    .content-room_subtitle {
        width: 400px;
    }
}

.room-image {
    margin-left: auto;
}

@media screen and (min-width: 1024px) {
    .room-image {
        position: absolute;
        left: 512px;
        top: -33px;
    }
}

@media screen and (min-width: 1280px) {
    .room-image {
        position: absolute;
        left: 640px;
        top: -30px;
    }
}

// =================================temporary photo============================
.temporary-photo_list {
    display: flex;
    margin-top: 32px;
}

@media screen and (min-width: 1024px) {
    .temporary-photo_list {
        margin-top: 176px;
    }
}

@media screen and (min-width: 1280px) {
    .temporary-photo_list {
        margin-top: 188px;
    }
}

.temporary-photo_first {
    display: none;
}

@media screen and (min-width: 1024px) {
    .temporary-photo_first {
        display: block;
        margin-right: 40px;
    }
}

// ====================================arrow button========================================
.arrow-svg {
    width: 32px;
    height: 32px;
}

.buton-arrow {
    border: none;
    background-color: transparent;
    padding: 0;
    margin-right: 32px;
}

.marg-reset {
    margin-right: 0;
}

.position-arrow {
    margin-top: 20px;
    margin-bottom: 20px;
}

@media screen and (min-width: 1024px) {
    .position-arrow {
        margin-top: 32px;
        margin-left: 325px;
        margin-bottom: 0;
    }
}

@media screen and (min-width: 1280px) {
    .position-arrow {
        margin-top: 40px;
        margin-left: 315px;
        margin-bottom: 0;
    }
}

// ===================================button-additionally===============================
.button-additionally:hover,
.button-additionally:focus {
    background-color: var(--main-white);
    border: 2px solid var(--accent-yellow);
}

.button-additionally {
    font-family: 'Montserrat';
    font-weight: 700;
    margin-left: auto;
    margin-right: auto;
    font-size: 16px;
    line-height: calc(19 / 16);
    letter-spacing: 0.02em;
    color: var(--accent-yellow);
    background-color: var(--main-black);
    border: 2px solid var(--background-main-color);
    border-radius: 25px;
    padding: 14px 64px;
    margin-bottom: 16px;

    transition: 600ms;
}

.pading-button {
    padding: 14px 37px;
    margin-bottom: 0;
}

.position-button-additionally {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

@media screen and (min-width: 1024px) {
    .position-button-additionally {
        position: absolute;
        right: 0;
        bottom: 145px;
    }
}

